#subsite-navigation {
  text-align: center;
  li {
    display: inline;
  }
}


.card-img-top{
  padding-top: 56.25%;
}
.list_group_menu_body_image{
  width: 240px;
  padding-top: 56.25%;
}

//banner
#banner{
  grid-column-start: 1;
  grid-column-end: -1;
  height:200px;
  background-size: cover;
}
.hidden-banner{
  height: 55px!important;
}

// a modifier dans le barceloneta
// header
html {
    #portal-header{
      display: grid;
      grid-template-columns: repeat(3, auto);
      align-items: center;
      #portal-searchbox{
        order: inherit;
        grid-column-start: 2;
        grid-row-start: 1;
        justify-self: end;
        max-width: max-content;
      }
      #portal-globalnav-wrapper{
        grid-column-start: 1;
        grid-column-end:-1;
      }
      #plone-breadcrumb{
        grid-column-start: 1;
        grid-column-end:-1;
      }
      #header-actions{
        grid-column-start: 3;
        grid-row-start: 1;
        justify-self: end;
      }
    }

}

//sub site

#subsite-navigation{
  grid-column-start: 2;
  grid-column-end: -1;
  li ul li a {
    min-width: 16em;
}
  & > ul{
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    flex-direction: row;
  }
  li{
    a{
      padding: 0.75rem 1rem;
    }
  }
  .has_subtree{
    position: relative;
    display: block;
    & .opener ~ ul {
      height: auto;
      transition: ease-in 0.35s;
  } 
    > label{
      position: absolute;
      right: 0em;
      top: 0em;
      height: 3em;
      width: 4em;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      padding-right: 0.5em;
    }
    & > input{
      display: none;
    }
    ul{
      position: absolute;
      z-index: 1000;
      background: #fff;
      border: solid 1px #e6e6e6;
      padding: 0;
      opacity: 0;
      visibility: hidden;
    }
    &:hover{
      & > ul{
        opacity: 1;
        visibility: visible;
      }
    }
    .has_subtree:hover > ul {
      left: 16em;
      top: 0;
  }
  }

}
